/***********************************************************************************************/
/* =Common Styles */
/***********************************************************************************************/

body {
    font-family: 'Raleway', sans-serif;
    color: #737272;
    line-height: 24px;
    font-size: 13px;
    letter-spacing: 1.2px;
    -webkit-font-smoothing: antialiased;
    padding-top: 68px;
    position: relative;
}

a {
    color: #fc6e51;
    -webkit-transition: all .25s ease-in-out;
    -moz-transition: all .25s ease-in-out;
    -ms-transition: all .25s ease-in-out;
    -o-transition: all .25s ease-in-out;
    transition: all .25s ease-in-out;
}

a:hover, a:focus {
    text-decoration: none;
}

img {
    max-width: 100%;
}

.dark-bg {
    background: #34353A;
}

.white-bg {
    background: #fff;
}

.without-padding {
    padding: 0;
}

.marg-0 {
    margin: 0;
}

.pad-b-0 {
    padding-bottom: 0!important;
}

.pad-t-100 {
    padding-top: 100px!important;
}

.bg-gray {
    background: #e5e5e5;
}

.form-control::-moz-placeholder {
    color: #494949;
}
.form-control:-ms-input-placeholder {
    color: #494949;
}
.form-control::-webkit-input-placeholder {
    color: #494949;
}

/***********************************************************************************************/
/* =Typography */
/***********************************************************************************************/

h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
    font-weight: 700;
    color: #1f2021;
    text-transform: uppercase;
}

h1, .h1, h2, .h2, h3, .h3 {
    margin-top: 0;
    margin-bottom: 20px;
}

h1, .h1 {
    font-family: 'Abel', sans-serif;
    font-size: 46px;
    font-weight: 400;
    letter-spacing: 2px;
}

h2 {
    font-size: 24px;
}

h3 {
    font-size: 17px;
}

h4 {
    font-size: 15px;
}

h5 {
    font-size: 12px;
}

h6 {
    font-size: 10px;
}

p {
    margin: 0;
}

.title-section hr {
    width: 15%;
}

.title-section  p {
    font-size: 15px;
    line-height: 26px;
    font-weight: 400;
    letter-spacing: 1.5px;
}

.pad-sec {
    padding: 100px 0!important;
}

.title-section {
    padding-left: 30px;
    padding-right: 30px;
}

.sep-section {
    height: 1px;
    border-bottom: 1px solid #EAEAEA;
}

/***********************************************************************************************/
/* =Buttons */
/***********************************************************************************************/

.btn {
    font-size: 13px;
    letter-spacing: 1px;
    padding: 14px 25px;
    border-radius: 1px;
    -webkit-transition: all .25s ease-in-out;
    -moz-transition: all .25s ease-in-out;
    -ms-transition: all .25s ease-in-out;
    -o-transition: all .25s ease-in-out;
    transition: all .25s ease-in-out;
}

.btn:active, .btn.active {
    outline: 0;
    background-image: none;
    -webkit-box-shadow: none;
    box-shadow: none;
}

.btn:focus, .btn:active:focus, .btn.active:focus, .btn.focus, .btn:active.focus, .btn.active.focus {
    outline: 0;
}

.btn-primary:hover, .btn-primary:focus, .btn-primary.focus,
.btn-primary:active, .btn-primary.active, .open>.dropdown-toggle.btn-primary {
    color: #fff;
    background-color: #000;
    border-color: #000;
}

.btn-xs {
    padding: 5px 14px;
    font-size: 11px;
    text-transform: uppercase;
    letter-spacing: 2px;
}

.btn-sm {
    padding: 8px 37px;
    font-size: 12px;
    -webkit-border-radius: 2px;
    -moz-border-radius: 2px;
    border-radius: 2px;
}

.btn-lg {
    padding: 15px 35px;
}

.btn-primary {
    text-transform: uppercase;
    background-color: #fc6e51;
    border-color: #fc6e51;
}

.btn-black {
    text-transform: uppercase;
    color: #fff;
    background-color: #000;
    border-color: #000;
}

.btn-black:hover {
    background-color: #fc6e51;
    border-color: #fc6e51;
    color: #fff;
}

.btn-clean {
    border-color: #F5F5F5;
    color: #F5F5F5;
    text-transform: uppercase;
    letter-spacing: 0;
}

.btn-clean:hover, .btn-clean:focus, .btn-clean:active {
    color: #fff;
}

.btn-dark {
    color: #FFF;
    text-transform: uppercase;
    background: rgba(34,34,34, .9);
    font-weight: 400;
    border-color: rgba(0,0,0, .7);
}

.btn-dark:hover, .btn-dark:active, .btn-dark:focus, .btn-dark:visited {
    color: rgba(255,255,255, .85);
    background: rgba(0,0,0, .7);
    outline: 0;
    box-shadow: none;
}

.btn-light {
    background: #f3f3f3;
    color: #000;
}

.call-btn {
    text-transform: uppercase;
    letter-spacing: 2px;
    padding: 12px 20px;
    font-size: 12px;
    border-radius: 0;
    border: none;
    outline: none;
    display: inline-block;
}

.btn-gray {
    color: #777;
    background: #e5e5e5;
}

/***********************************************************************************************/
/* =Form */
/***********************************************************************************************/

.form-group {
    margin-bottom: 20px;
}

.form-control {
    font-size: 12px;
    color: #555;
    background-color: #fff;
    background-image: none;
    border: 1px solid #ccc;
    border-radius: 1px;
    height: auto;
    padding: 9px 12px;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    box-shadow: none;
    -webkit-transition: all 0.2s cubic-bezier(0.000, 0.000, 0.580, 1.000);
    -moz-transition: all 0.2s cubic-bezier(0.000, 0.000, 0.580, 1.000);
    -o-transition: all 0.2s cubic-bezier(0.000, 0.000, 0.580, 1.000);
    -ms-transition: all 0.2s cubic-bezier(0.000, 0.000, 0.580, 1.000);
    transition: all 0.2s cubic-bezier(0.000, 0.000, 0.580, 1.000);
}

.form-control:focus {
    color: #111;
    border-color: rgba(0,0,0, .7);
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    box-shadow: none;
    outline: none;
}

/***********************************************************************************************/
/* =Preloader */
/***********************************************************************************************/

#preloader {
    background-color: #FFF;
    bottom: 0;
    left: 0;
    position: fixed;
    right: 0;
    top: 0;
    z-index: 999999;
}

#loading-animation {
    background-image: url("../images/loading.GIF") ;
    opacity: 0.7;
    background-position: center center;
    background-repeat: no-repeat;
    height: 200px;
    left: 50%;
    margin: -100px 0 0 -100px;
    position: absolute;
    top: 50%;
    width: 200px;
    border-radius: 4px;
}

/************************************************************************
// =Navbar
*************************************************************************/

.navbar-default {
    background: #fff;
    border: none;
    width: 100%;
    margin-bottom: 0;
    position: absolute;
    top: 0;
    border-bottom: 2px solid #f0f0f0;
}
.navbar.active {
    position: fixed;
    opacity: 0.95;
    border: 1px solid #E8E8E8;
}

.navbar-nav {
    margin: 7.5px -10px;
}

.navbar-default .navbar-nav > li > a {
    color: #060606;
    text-transform: uppercase;
    -webkit-transition: color .3s ease-in-out;
    -moz-transition: color .3s ease-in-out;
    -ms-transition: color .3s ease-in-out;
    -o-transition: color .3s ease-in-out;
    transition: color .3s ease-in-out;
}

.navbar-default .navbar-nav > .active > a, .navbar-default .navbar-nav > .active > a:hover, .navbar-default .navbar-nav >
.active > a:focus {
    background: transparent;
    color: #fc6e51;
}

.navbar-default .navbar-nav > li > a:hover, .navbar-default .navbar-nav > li > a:focus {
    color: #fc6e51;
}

.navbar-brand {
    margin-top: 5px;
}

.dropdown-menu {
    top: 117%;
    font-size: 12px;
    text-transform: uppercase;
    -webkit-box-shadow: none;
    box-shadow: none;
    border-radius: 2px;
    border-top: 0;
    padding: 12px 5px;
}

.dropdown-menu>li>a {
    padding: 3px 15px;
    line-height: 1.7;
    letter-spacing: 2px;
}

.dropdown-menu>li>a:focus, .dropdown-menu>li>a:hover {
    background-color: #fff;
    color: #fc6e51;
}

.navbar-default .navbar-nav>.open>a, .navbar-default .navbar-nav>.open>a:focus, .navbar-default .navbar-nav>.open>a:hover {
    background-color: #fff;
    color: #fc6e51;
}

.navbar-default .dropdown:hover>.dropdown-menu {
    display: block;
}

