/*--------------------
  About widget
----------------------*/

.widget-about h3 {
    text-align: center;
    border-bottom: 0;
}

.about-widget-img {
    width: 150px;
    max-height: 200px;
    margin: 30px auto 0 auto;
    text-align: center;
    padding-bottom: 30px;
}

.about-widget-img img {
    border-radius: 50%;
}

.widget-about p {
    text-align: center;
}

.widget-social {
    text-align: center;
    list-style: none;
    padding-left: 0;
    margin-bottom: 0;
    padding-top: 20px;
}

.widget-social li {
    display: inline-block;
}

.widget-social li a {
    display: block;
    width: 30px;
    height: 30px;
    margin: 0 3px 6px 3px;
    border-radius: 15px;
    background-color: #f5f5f5;
    line-height: 30px;
    text-align: center;
}

.widget-social li a:hover {
    background-color: #272B2F;
    color: #fff;
}

/*--------------------
  Tags
----------------------*/

.tagcloud a, .post-tag a {
    display: inline-block;
    margin: 0 2px 8px 0;
    padding: 5px 7px;
    border: 1px solid #e9e9e9;
    color: #777;
    font-size: 11px;
    font-weight: 400;
    text-transform: uppercase;
    text-decoration: none;
    letter-spacing: 1px;
    -webkit-transition: all 0.27s cubic-bezier(0.000, 0.000, 0.580, 1.000);
    -moz-transition: all 0.27s cubic-bezier(0.000, 0.000, 0.580, 1.000);
    -o-transition: all 0.27s cubic-bezier(0.000, 0.000, 0.580, 1.000);
    -ms-transition: all 0.27s cubic-bezier(0.000, 0.000, 0.580, 1.000);
    transition: all 0.27s cubic-bezier(0.000, 0.000, 0.580, 1.000);
}

.tagcloud a:hover, .tagcloud a:focus, .tagcloud a:active {
    text-decoration: none;
    border-color: #333;
    color: #111;
}

/*--------------------
  Recent Posts
----------------------*/

.widget-posts-img {
    float: left;
    margin: 0 7px 0 0;
    width: 80px;
    height: 80px;
}

.widget-posts li a {
