/***********************************************************************************************/
/* =Footer */
/***********************************************************************************************/

#footer-section {
    padding: 80px 0;
    background: #F7F7F7;
    border-top: 1px solid #EEEEEE;
}

ul.footer-social-links {
    list-style-type: none;
    padding: 0;
}

ul.footer-social-links li {
    display: inline-block;
    margin-left: 30px;
}

ul.footer-social-links li:first-child {
    margin: 0;
}

ul.footer-social-links li a {
    font-size: 15px;
    color: #4F4F4F;
    display: block;
    text-transform: uppercase;
}

ul.footer-social-links li a:after {
    content: "";
    display: block;
    left: 0;
    right: 0;
    height: 1px;
    background: #fe8869;
    opacity: 0;
    -webkit-transform: translateY(5px);
    -moz-transform: translateY(5px);
    -ms-transform: translateY(5px);
    -o-transform: translateY(5px);
    transform: translateY(5px);
    -webkit-transition: all .3s ease;
    -moz-transition: all .3s ease;
    -ms-transition: all .3s ease;
    -o-transition: all .3s ease;
    transition: all .3s ease;
}

ul.footer-social-links li a:hover:after {
    opacity: 1;
    -webkit-transform: translateY(0);
    -moz-transform: translateY(0);
    -ms-transform: translateY(0);
    -o-transform: translateY(0);
    transform: translateY(0);
}

p.copyright {
    padding-top: 40px;
}

/***********************************************************************************************/
/* =Back to top */
/***********************************************************************************************/

.back-to-top {
    text-align: center;
    background: #ed4d2b;
    color: #fff;
    display: none;
    width: 40px;
    height: 40px;
    line-height: 40px;
    font-size: 12px;
    position: fixed;
    right: 25px;
    bottom: 25px;
    opacity: 0.7;
    -webkit-transition: all .3s ease-in-out;
    -moz-transition: all .3s ease-in-out;
    -ms-transition: all .3s ease-in-out;
    -o-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
    cursor: pointer;
    z-index: 9999
}

.back-to-top:hover {
    opacity: .9;
}

/***********************************************************************************************/
/* Responsive Part */
/***********************************************************************************************/

@media screen and (max-width: 1200px) {

    #about-section .about {
        padding-top: 40px;
    }

}

@media screen and (max-width: 992px) {

    body {
        padding-top: 55px;
    }

    .navbar .container {
        width: 95%!important;
    }

    .entry-post {
        padding-bottom: 35px;
    }

    #type-masory {
        width: auto;
    }

    #about-section .about {
        padding: 90px 70px;
    }

    .member-team .magnifier .magnifier-inner {
        padding: 0 10px;
    }

    .member-team .magnifier .magnifier-inner p:last-of-type {
        display: none;
    }

    ul.price-table {
        margin-left: 0;
        margin-right: 0;
    }

    .banner-image {
        margin-top: 40px!important;
    }

    .gallery-masory {
        width: auto;
    }

    .contactform {
        padding: 80px 10px;
    }

    .portfolio-desc .col-sm-4 {
        margin-bottom: 20px;
    }

    .portfolio-rel figure {
        margin-bottom: 20px;
    }

    .form-inline .form-control, .form-inline .form-group {
        display: block;
    }

    .newsletter-field {
        width: 100%;
    }

    #subscribe-section #subscribe-button {
        display: block;
        width: 100%;
    }

    #blog-head {
        text-align: center!important;
    }

    #search-form {
        margin-top: 30px;
    }

    .breadcrumb {
        text-align: center;
    }

}

@media screen and (max-width: 768px) {

    .navbar .container {
        width: auto;
    }

    .big-title-sec .view-more a {
        margin: 5px 0;
    }

    .hero-content h1 {
        font-size: 38px;
    }

    .soon-countdown div span:first-child {
        font-size: 60px;
    }

    .creative-content-left {
        padding-top: 0;
    }

    #creative-section-1 .view-more {
        text-align: center!important;
    }

    #creative-section-2 .view-more {
        margin-bottom: 80px;
        text-align: center!important;
    }

    .about-studio, .what-do {
        padding-bottom: 35px;
    }

    .member-team {
        margin-bottom: 30px;
    }

    .member-team .magnifier .magnifier-inner {
        padding: 0 30px;
    }

    .member-team .magnifier .magnifier-inner p:last-of-type {
        display: block;
    }

    .banner-content {
        margin-top: 80px;
        margin-bottom: 60px;
        text-align: center;
    }

    .banner-image {
        text-align: center;
        margin-bottom: 40px;
    }

    .client img {
        width: 70%!important;
    }

    .about-me h4 a {
        margin-bottom: 30px;
    }

}

@media screen and (max-width: 640px) {

    h1, .h1 {
        font-size: 40px;
    }

    .hero-content h1 {
        font-size: 33px;
    }

    .soon-countdown div span:first-child {
        font-size: 50px;
    }

    p.hero {
        font-size: 14px;
        line-height: 24px;
    }

    .big-title-sec .big-title {
        font-size: 20px;
    }

    .big-title-sec .big-subtitle {
        font-size: 28px;
    }

    .big-title-sec p {
        font-size: 14px;
    }

    #action-section {
        text-align: center;
    }

    #action-section h2 {
        line-height: 1.4;
    }

    #action-section .btn+.btn {
        margin-left: 0;
        margin-top: 20px;
    }

    .client img {
        width: 100%!important;
    }

}

@media screen and (max-width: 480px) {
    .back-to-top {
        visibility: hidden;
    }
}
